import React from 'react';

import {
    parsePlaceholderKey,
    popVisibleRenderingsFromPlaceholdersMap,
    RenderingContext,
} from '@uniformdev/common-client';
import { useUniformContext } from '../contexts';
import { CreateRendering } from '../processHtml';

interface BasePlaceholderRenderProps {
    placeholderKey: string;
    renderingContext: RenderingContext;
}

export function Placeholder({ placeholderKey, renderingContext }: BasePlaceholderRenderProps) {
    const { logger } = useUniformContext();
    if (placeholderKey === undefined || placeholderKey === null) {
        throw new Error('The props.placeholderKey is not defined');
    }

    placeholderKey = parsePlaceholderKey(placeholderKey);

    const visibleRenderings = popVisibleRenderingsFromPlaceholdersMap(
        renderingContext.placeholders,
        placeholderKey,
        logger
    );

    return (
        <>
            {visibleRenderings.map((rendering, index) => (
                <CreateRendering
                    key={index}
                    index={index}
                    rendering={rendering}
                    placeholderKey={placeholderKey}
                    renderingContext={renderingContext}
                />
            ))}
        </>
    );
}
